<template>
  <div class="expert-team-detail">
    <div class="team-header">
      <div class="container">
        <div class="section-header text-center">
          <h2>{{ teamTitle }}</h2>
          <p class="section-subtitle">{{ teamDesc }}</p>
        </div>
      </div>
    </div>
    
    <div class="team-content">
      <div class="container">
        <div class="row">
          <div v-for="expert in experts" :key="expert.name" class="col-md-4 col-sm-6 mb-4">
            <div class="expert-detail-card text-center">
              <div class="expert-avatar-container">
                <img v-if="expert.avatar" :src="expert.avatar" class="expert-avatar-img" alt="专家头像" />
                <div v-else class="avatar-placeholder"><i class="fas fa-user"></i></div>
              </div>
              <div class="expert-info">
                <h4>{{ expert.name }}</h4>
                <div class="expert-title">{{ expert.title }}</div>
                <p class="expert-desc">{{ expert.desc }}</p>
              </div>
            </div>
          </div>
        </div>
        
        <div class="text-center mt-5">
          <button class="btn-return" @click="$router.back()">
            <i class="fas fa-arrow-left"></i> 返回上一页
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ExpertTeamDetail',
  data() {
    return {
      experts: [],
      teamTitle: '',
      teamDesc: ''
    }
  },
  created() {
    const teamType = this.$route.params.teamType;
    // 这里可以根据实际需求从后端或本地获取数据
    const teamMap = {
      mingli: {
        title: '命理顾问团队',
        desc: '私人命理顾问团队，精通多年命理实践经验，精通八字、紫微斗数',
        experts: [
          { name: '王大师', title: '首席命理师', desc: '20年命理实战，擅长八字分析', avatar: '' },
          { name: '李老师', title: '命理顾问', desc: '紫微斗数专家，客户好评如潮', avatar: '' },
          { name: '张顾问', title: '奇门遁甲专家', desc: '15年奇门遁甲研究经验', avatar: '' }
        ]
      },
      ai: {
        title: '数据智能专家组',
        desc: '智能算法架构师团队，深度学习技术赋能传统命理研究',
        experts: [
          { name: '张博士', title: 'AI算法专家', desc: '人工智能与命理结合的先行者', avatar: '' },
          { name: '陈工', title: '数据科学家', desc: '大数据建模与分析', avatar: '' },
          { name: '刘教授', title: '机器学习专家', desc: '深度学习与传统命理学结合研究', avatar: '' }
        ]
      },
      medicine: {
        title: '中医养生专家组',
        desc: '体质调理专家，融合中医脉诊与生物数据分析技术',
        experts: [
          { name: '赵医生', title: '中医师', desc: '中医体质辨识与调理', avatar: '' },
          { name: '孙教授', title: '养生顾问', desc: '健康管理与养生方案设计', avatar: '' },
          { name: '钱医师', title: '针灸专家', desc: '传统针灸与现代医学结合', avatar: '' }
        ]
      },
      psychology: {
        title: '心理科学专家组',
        desc: '行为心理研究员，基于大数据的心理特征建模与干预方案设计',
        experts: [
          { name: '刘博士', title: '心理学家', desc: '心理特征建模与干预', avatar: '' },
          { name: '王老师', title: '心理咨询师', desc: '多年心理咨询经验', avatar: '' },
          { name: '周教授', title: '行为心理学专家', desc: '行为模式分析与预测', avatar: '' }
        ]
      },
      nature: {
        title: '自然科学专家组',
        desc: '环境与地理科学专家，研究自然环境对人体的影响',
        experts: [
          { name: '吴教授', title: '环境科学家', desc: '环境因素与人体健康关系研究', avatar: '' },
          { name: '郑博士', title: '地理学专家', desc: '地理环境对人体影响分析', avatar: '' },
          { name: '林研究员', title: '天文学家', desc: '天文周期与人体生理节律研究', avatar: '' }
        ]
      }
    };
    if (teamMap[teamType]) {
      this.teamTitle = teamMap[teamType].title;
      this.teamDesc = teamMap[teamType].desc;
      this.experts = teamMap[teamType].experts;
    }
  }
}
</script>

<style scoped>
.expert-team-detail {
  min-height: 100vh;
  background-color: #f9f9f9;
}

.team-header {
  background-image: url('../assets/img/platform-bg.png'); /* 请替换为您的实际背景图 */
  background-size: cover;
  background-position: center;
  padding: 80px 0 60px;
  position: relative;
  color: #fff;
}

.team-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(142, 68, 173, 0.85) 0%, rgba(52, 152, 219, 0.85) 100%);
  z-index: 1;
}

.team-header .container {
  position: relative;
  z-index: 2;
}

.section-header h2 {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 15px;
  color: #fff;
}

.section-subtitle {
  font-size: 18px;
  max-width: 700px;
  margin: 0 auto;
  color: rgba(255, 255, 255, 0.9);
}

.team-content {
  padding: 60px 0;
  position: relative;
  margin-top: -30px;
}

.expert-detail-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  padding: 30px 20px;
  height: 100%;
  transition: all 0.3s ease;
  border: 1px solid rgba(0, 0, 0, 0.05);
  position: relative;
  overflow: hidden;
}

.expert-detail-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(90deg, #8e44ad, #3498db);
}

.expert-detail-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.12);
}

.expert-avatar-container {
  width: 120px;
  height: 120px;
  margin: 0 auto 25px;
  position: relative;
}

.expert-avatar-img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #fff;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.avatar-placeholder {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: linear-gradient(135deg, #8e44ad 0%, #3498db 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 48px;
  color: #fff;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.expert-info {
  padding-top: 10px;
}

.expert-detail-card h4 {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 10px;
  color: #2c3e50;
}

.expert-title {
  color: #8e44ad;
  font-size: 16px;
  margin-bottom: 15px;
  font-weight: 500;
  position: relative;
  display: inline-block;
  padding-bottom: 10px;
}

.expert-title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 2px;
  background: #8e44ad;
}

.expert-desc {
  color: #666;
  font-size: 14px;
  line-height: 1.6;
}

.btn-return {
  background: linear-gradient(135deg, #8e44ad 0%, #3498db 100%);
  color: white;
  border: none;
  padding: 12px 30px;
  border-radius: 30px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(142, 68, 173, 0.3);
}

.btn-return:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(142, 68, 173, 0.4);
}

.btn-return i {
  margin-right: 8px;
}

/* 响应式调整 */
@media (max-width: 768px) {
  .team-header {
    padding: 60px 0 40px;
  }
  
  .section-header h2 {
    font-size: 28px;
  }
  
  .expert-avatar-container {
    width: 100px;
    height: 100px;
  }
  
  .expert-detail-card {
    padding: 20px 15px;
  }
}
</style>


